html, body{
  min-height:100%;
}

body{
  height:100vh;
}

.contact_us {
    position: relative;
    background: url(images/contactus_bg.jpg) center;
    overflow: hidden;
    background-size:100% 100%;
    background-attachment: fixed;
    color: #ffffff;
    font-weight: bold;
    z-index: 2;
}

.contact_us:before {
    content: '';
    background: rgba(14, 15, 15, 0.75);
    position: absolute;
    bottom: 0;
    top: 0;
    left: 0;
    right: 0;
    z-index: -1;
}

.contact_us_inner{
  background: rgba(14, 15, 15, 0.4);
  border-radius: 10px;
  margin: 0px 150px;
  height: fit-content;
  min-height: fit-content;
}

.btn{
  width: 150px;
  height: 45px;
  font-weight: bold;
  background-color: red;
  border: 2px solid red;
  color: #fff;
  font-size: 16px;
}

.btn:hover{
  background-color: transparent;
  border: 2px solid red;
  color: red;
}

#accordionPanelsStayOpenExample{
  margin:0px 150px;
}


